import React from 'react';

import blogs from "../../api/blogs";

import './blog.css';

const blog = () =>{
    return (
      <div className='skillContent'>
        {
          blogs.map((blog, index)=>{
          let smart = 'container'
          index%2 ? smart = 'container reverse': smart = 'container'; 
            return( 
                <div key={ blog.id } className={smart}>
                    <div className='picture'><a href={ blog.link } rel="noopener noreferrer" target='_blank'><img src={ blog.img } alt={ blog.alt }></img></a></div>
                    <div className='detail'>
                        <div className='company'>{ blog.article }</div>
                        <div className='description'>{ blog.intro }</div>
                    </div>
                </div>
            )
        })
      }
      </div>
    )
}

export default blog;