import React from "react";
import { Route, Link } from "react-router-dom";

import Aux from "../../hoc/hoc";
import Home from "../../../containers/home/home";
import Work from "../../../containers/work/work";
import Blog from "../../../containers/blog/blog";
// import Social from "../socialmedia/socialmedia"

import logo from '../../../assets/images/logo/logo.png';

import './nav.css';

const nav = () =>{
    let wBold = '';
    let bBold = '';
    let url = window.location.href;
    let urlArr = url.split('/');
    let urlLast = urlArr[urlArr.length-1];

    if(urlLast === 'work'){
        wBold = 'bold';
        bBold = '';
    }
    else if(urlLast === 'blog'){
        wBold = '';
        bBold = 'bold';
    }
    else{
        wBold = '';
        bBold = '';
    }

    const hamBut = ()=> {
        const bar1 = document.querySelector('.bar1');
        const bar2 = document.querySelector('.bar2');
        const bar3 = document.querySelector('.bar3');
        const list = document.querySelector('.list');
        const contain = document.querySelector('.containBar');

        bar1.classList.toggle("change");
        bar2.classList.toggle("change");
        bar3.classList.toggle("change");
        list.classList.toggle("change");
        contain.classList.toggle("change");
      }

    return(
        <Aux>
            <div className='fix'>
                <div className='nav'>
                    <div className='navbar'>
                        <Link to='/'><img className='navLogo' src={logo} alt='logo'/></Link>

                        <div className='hamburger'>
                            <div className="containBar" onClick={hamBut}>
                                <div className="bar1"></div>
                                <div className="bar2"></div>
                                <div className="bar3"></div>
                            </div>

                            <div className='list'>
                                <ul className='largeScreen navlist'>
                                    <li className='navlink'><Link to='/work' className={ wBold } onClick={hamBut}>What I've done</Link></li>
                                    <li className='navlink'><Link to='/blog' className={ bBold } onClick={hamBut}>Blog</Link></li>
                                </ul>
                                {/* <div className='navSocial'><Social/></div> */}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
                <Route path='/' exact component={Home}/>
                <Route path='/work' component={Work}/>
                <Route path='/blog' component={Blog}/>
        </Aux>
        
    )
}

export default nav;