import React from 'react';

import skills from "../../api/skills";

import './skill.css';

const skill = () =>{
    return (
      <div className='skillContent'>
        {
          skills.map((skill, index)=>{
          // reverse stuff
          // if index % 2 = 0 apply reverse
          let smart = 'container'
          index % 2 ? smart = 'container reverse': smart = 'container';
          
              return( 
                  <div key={ skill.id } className={smart}>
                    <div className='picture'><a href={ skill.link } rel="noopener noreferrer" target='_blank'><img src={ skill.img } alt={ skill.alt }></img></a></div>
                    <div className='detail'>
                      <div className='company'>{ skill.company }</div>
                      <div className='role'>{ skill.role }</div>
                      <div className='description'>{ skill.description }</div>
                      <div className='achievement'><span className='bold'>Achievement:&nbsp;</span>{ skill.acheivement }</div>
                    </div>
                  </div>
              )
            })
      }
      </div>
    )
}

export default skill;