import React from "react";

import ln from "../../../assets/images/social/ln.png";
import tw from "../../../assets/images/social/tw.png";
import fb from "../../../assets/images/social/fb.png";
import gh from "../../../assets/images/social/gh.png";

import './socialmedia.css'

const socialmedia = () =>{
    return(
        <div className='social'>
            <div className="socialBlock"><a href='https://www.linkedin.com/in/solomon-igori/' rel='noopener noreferrer' target='_blank'><img src={ln} className='socialpic' alt='linkedin link'/></a></div>
            <div className="socialBlock"><a href='https://twitter.com/SolomonIgori' rel='noopener noreferrer' target='_blank'><img src={tw} className='socialpic' alt='twitter link'/></a></div>
            <div className="socialBlock"><a href='https://www.facebook.com/solomonigori' rel='noopener noreferrer' target='_blank'><img src={fb} className='socialpic' alt='facebook link'/></a></div>
            <div className="socialBlock"><a href='https://github.com/Igorisolomon' rel='noopener noreferrer' target='_blank'><img src={gh} className='socialpic' alt='github link'/></a></div>
        </div>
    )
}

export default socialmedia;
